import { Component, OnInit } from '@angular/core';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';

@Component({
  selector: 'app-see-ledgers',
  templateUrl: './see-ledgers.page.html',
  styleUrls: ['./see-ledgers.page.scss'],
})
export class SeeLedgersPage implements OnInit {

  public filterTerm:any;
  public ledgers:any;
  owners: string;
  constructor( public apiCall: ApicallService , public global: GlobalService) { }

  ngOnInit() {
    this.getOwner();
  }
  //  Get Owners
  async getOwner() {
    await this.apiCall.api_getOwner();
    await this.global.Owner.subscribe( owner => {
      this.owners = owner;
    })
  }
  async selectOwner($event) {
    await this.apiCall.api_getLedgers($event.target.value);
    await this.global.Ledgers.subscribe( ledgers => {
      this.ledgers = ledgers;
    })
  }

}
